.page{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    padding: 40px;
}
.card{
    border: 1px solid #eee;
    margin: 20px 20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    text-decoration: none;
    color: black;
    
}
.imgdiv{
    border: 1px solid blueviolet;
}
.photo{
    width: 200px;
    height:200px;
}
.category{
    color: rgb(182, 180, 180);
}

.sortfilter{
    display: flex;
    justify-content: flex-start;
    gap:60px;
    
    margin-left: 60px;
}

